export default {
	data() {
		return {
			
		}
	},
	methods: {
		opennew:function(id){
			uni.navigateTo({
				url: '../'+id+'/'+id
			});
			
		},
		godelete:function(index){
			uni.showModal({
			    title: '温馨提示',
			    content: '您确定删除此收货地址吗？',
				confirmColor:'rgb(0, 138, 5)',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		}
	}
}
